import { React, useState, useLayoutEffect } from 'react';
import { Redirect } from 'react-router-dom';
import NotFound from '../../Auth/NotFound/NotFound';
import MaterialTable from 'material-table';
import RadioGroup from '../../../components/Controls/RadioGroup';
import Spinner from '../../../components/UI/Spinner/Spinner';
import classes from './GradesList.module.css';
import SnackBar from '../../../components/UI/SnackBar/SnackBar';
import axios from 'axios';

const attendanceStatusItems = [
  { id: 'A', title: 'A' },
  { id: 'N/A', title: 'N/A' },
];

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const GradesList = (props) => {
  const [snackStatus, setSnackStatus] = useState({
    openSnackBar: false,
    snackBarMessage: '',
    severity: '',
  });
  const [auxRegister, setAuxRegister] = useState(false);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [notFound, setNotFound] = useState(false);

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, [auxRegister]);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/grades-list/' + props.match.params.group
    axios
      .get(
        '/grades-list/' + props.match.params.group
      )
      .then((response) => {
        console.log(response.data);
        setData(response.data);
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        setLoading((loading) => !loading);
        if (error.response) {
          //Redirige si el usuario no esta inscrito a cierto grupo o si el grupo no existe
          if (error.response.data.message == 'Pagina no encontrada') {
            setNotFound(true);
          } else {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            window.alert('Error: ' + error.response);
          }
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const handleRowUpdate = (status, rfc, resolve) => {
    //url = `/auth/grades-list/${props.match.params.group}/${rfc}`, status
    axios
      .put(`/auth/grades-list/${props.match.params.group}/${rfc}`, status)
      .then((res) => {
        setAuxRegister((auxRegister) => !auxRegister);
        resolve();
        setSnackStatus({
          openSnackBar: true,
          severity: 'success',
          snackBarMessage: 'Calificacin registrada correctamente',
        });
      })
      .catch((error) => {
        setSnackStatus({
          openSnackBar: true,
          severity: 'error',
          snackBarMessage: error.response.data.message,
        });
      });
  };

  let courseEnrollmentTable = (
    <div className={classes.table}>
      <MaterialTable
        title={'Calificaciones del grupo: ' + props.match.params.group}
        columns={[
          { title: 'NOMBRE', field: 'name', editable: 'never' },
          {
            title: 'APROBADO / NO APROBADO',
            field: 'status',
            editComponent: (props) => (
              <RadioGroup
                name='gradingStatus'
                value={props.value}
                onChange={(e) => {
                  props.onChange(e.target.value);
                }}
                items={attendanceStatusItems}
              />
            ),
          },
          { title: 'Active', field: 'active', hidden: true },
          { title: 'RFC', field: 'rfc', hidden: true },
        ]}
        data={data}
        editable={{
          onRowUpdate: (newData) =>
            new Promise((resolve) => {
              handleRowUpdate(newData.status, newData.rfc, resolve);
            }),
        }}
        options={{
          actionsColumnIndex: -1,
          pageSize: 30,
          pageSizeOptions: [30, 50],
          headerStyle: {
            backgroundColor: '#7395ae',
            color: '#FFF',
            fontSize: '13px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
        }}
        localization={{
          header: {
            actions: 'CALIFICAR',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No cuenta con cursos asignados',
          },
        }}
      />
    </div>
  );

  if (loading) {
    courseEnrollmentTable = <Spinner />;
  }

  if (notFound) {
    courseEnrollmentTable = <Redirect to='/notFound' component={NotFound} />;
  }

  return (
    <div style={styles}>
      {courseEnrollmentTable}{' '}
      <SnackBar
        openSnackBar={snackStatus.openSnackBar}
        closeSnackBar={() => setSnackStatus({ openSnackBar: false })}
        snackBarMessage={snackStatus.snackBarMessage}
        severity={snackStatus.severity}
      />
    </div>
  );
};

export default GradesList;
